import React, { Component } from "react";
import { Route, Switch, Link } from "react-router-dom";
import { Tabs } from "antd";
import PicCon from "./picCon/picCon";
import Header from "../../components/head/head";
import CardItem from "../../components/cardItem/cardItem";
import "./store.scss";
import storeCom from "../../components/storeCom/storeCom";
const TabPane = Tabs.TabPane;
class Store extends Component {
  state={
    activeKey:''
  }
  componentWillMount=()=>{
    const toId = localStorage.getItem('toId');
    const key=toId.slice(2,3).toString();
    this.setState({activeKey:key})
    console.log(this.props)
  }
  render() {
    return (
      <div className="store">
        <Header />
        <Tabs  className='content' 
               onChange={this.tabChange} 
               defaultActiveKey={this.state.activeKey}
               >
          <TabPane tab="家庭" key="1">
                    <PicCon></PicCon>
          </TabPane>
          <TabPane tab="亲子" key="2">
            <PicCon></PicCon>
          </TabPane>
          <TabPane tab="婚纱" key="3">
                    <PicCon></PicCon>
          </TabPane>
          <TabPane tab="写真" key="4">
                    <PicCon></PicCon>
          </TabPane>
          <TabPane tab="日系" key="5">
            <PicCon></PicCon>
          </TabPane>
          <TabPane tab="古风" key="6">
                    <PicCon></PicCon>
          </TabPane>
          <TabPane tab="cos" key="7">
                    <PicCon></PicCon>
          </TabPane>
          <TabPane tab="234" key="8">
            <PicCon></PicCon>
          </TabPane>
       
        </Tabs>
      </div>
    );
  }
}

export default Store;
