import React, { Component } from "react";
import { Table, Select, Divider, Tag, Button } from "antd";
import UserInfo from './userInfo'

import * as actions from "../../store/actions/index";
import { connect } from "react-redux";

import Header from "../../components/head/head";
// import {getCartList} from '../../api'
import api from "../../api";
import "./shop.scss";

const Option = Select.Option;
class Shop extends Component {
  state = {
    cartList: [],
    count: 0
  };

  componentDidMount() {
    this.getCart();

  }
  getCart = () => {
    const { getCartList } = api;
    getCartList().then(({ data }) => {
      this.setState({ cartList: data.data.list, count: data.data.count });
    });
  };
  postNum = () => {
    const { getCartList } = api;
  };
  handleChangNum = (val, option) => {
    // console.log(val,option)
    this.getCart();
  };
  exit = ()=>{
    this.props.history.push('/')
  }
  render() {
    const columns = [
      {
        className: "Pic",
        title: "全选",
        dataIndex: "all",
        key: "all",
        render: text => <img src={text} />
      },
      {
        title: "商品信息",
        dataIndex: "info",
        key: "info",
        render: text => (
          <>
            <span>{text}</span>
            <span>删除</span>
          </>
        )
      },
      {
        title: "单价",
        dataIndex: "price",
        key: "price"
      },
      {
        title: "数量",
        key: "nums",
        dataIndex: "nums",
        render: text => (
          <Select
            defaultValue={text}
            style={{ width: 60 }}
            onChange={this.handleChangNum}
          >
            <Option value="1">{text}</Option>
            <Option value="2">2</Option>
            <Option value="3">3</Option>
            <Option value="4">4</Option>
            <Option value="5">5</Option>
          </Select>
        )
      },
      {
        title: "总计",
        key: "totalPrice",
        dataIndex: "totalPrice"
      }
    ];
    const rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(
          `selectedRowKeys: ${selectedRowKeys}`,
          "selectedRows: ",
          selectedRows
        );
      },
      getCheckboxProps: record => ({
        disabled: record.name === "Disabled User", // Column configuration not to be checked
        name: record.name
      })
    };
    return (
      <section className="shopCar">
        <Header />
        <section class="content">
          <header>
            <span
              className="goShop"
              onClick={() => {
                this.props.history.push("/");
              }}
            >
              &lt;继续购物
            </span>

            {/* <div
              onClick={() => {
                console.log(this.props);
                // actions.test()
                this.props.test()
              }}
            >
              共件商品{this.props.count}
            </div> */}

            {/* <span>我的购物袋({this.state.count})</span> */}
          </header>
          <UserInfo></UserInfo>
          <div className="box">
          <section className="table">
            <p>租赁商品</p>
              <Table
                rowSelection={rowSelection}
                columns={columns}
                dataSource={this.state.cartList}
                pagination={false}
              />
            </section>
            <section className="table">
            <p>预约套餐</p>
            <Table
                rowSelection={rowSelection}
                columns={columns}
                dataSource={this.state.cartList}
                pagination={false}
              />
              </section>
            <section className="account">
              <div>订单总结</div>
              <Button className='jiesuan'>立即结算</Button>
              <span>商品价格:￥4444</span>
              <span>总价￥4444</span>
            </section>
            <section className="exit">
            <Button type="danger" onClick={this.exit}>退出登录</Button>
            </section>
          </div>
        </section>
      </section>
    );
  }
}
const mapStateToProps = state => state.Test;
export default connect(
  mapStateToProps,
  actions
)(Shop);
