import React, { Component } from "react";
import api from "../../api/index";
import { Link } from "react-router-dom";
import { Button, Modal, Calendar, Input, Tooltip, Icon, Spin } from "antd";
import ReactCompareImage from "react-compare-image";
import "./packageDet.scss";

export default class packageDet extends Component {
  state = {
    imgList: [],
    visible: false
  };
  componentDidMount() {
    console.log(this.props)
    api.getPackageDet().then(({ data }) => {
      //    console.log(data)
      this.setState({
        imgList: data.data
      });
    });
  }
  showModal = () => {
    this.setState({
      visible: true
    });
  };

  handleOk = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };

  handleCancel = e => {
    console.log(e);
    this.setState({
      visible: false
    });
  };
  onPanelChange = (value, mode) => {
    console.log(value, mode);
  };

  render() {
    return (
      <div
        className="packageDet"
        onClick={() => {
          console.log();
        }}
      >
        <Link to="/store" className="goMore">
          继续逛逛
        </Link>

        <div className="picShow">
          <div className="compare">
            <ReactCompareImage
              leftImage="https://bojueapp.oss-cn-hangzhou.aliyuncs.com/201810/ef8cf6aa51.jpg"
              rightImage="https://bojueapp.oss-cn-hangzhou.aliyuncs.com/201810/2dbfe5ccea.jpg"
              skeleton={<div>loading</div>}
            />
          </div>
          <img src="http://k.zol-img.com.cn/sjbbs/7692/a7691515_s.jpg" />
          <img src="https://bojueapp.oss-cn-hangzhou.aliyuncs.com/201810/a990842c39.jpg" />

          <img src="http://k.zol-img.com.cn/sjbbs/7692/a7691515_s.jpg" />
        </div>
        <div className="order">
          <p>拨打电话12345645665咨询</p>
          <Button onClick={this.showModal}>点击加入套餐</Button>
        </div>
        <Modal
          title="提交订单"
          okText="去拍摄"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
        >
          <Input
            placeholder="输入您的手机号"
            prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
            suffix={
              <Tooltip title="我们将会联系您">
                <Icon type="info-circle" style={{ color: "rgba(0,0,0,.45)" }} />
              </Tooltip>
            }
          />
           <span>请选择拍摄日期</span>
          <Calendar
            fullscreen={false}
            onPanelChange={this.onPanelChange}
            onChange={this.onPanelChange}
          />
        </Modal>
      </div>
    );
  }
}
