import React from 'react';
import './commentItem.scss'

export default function CommentItem(props){
   if(!props.is){
    return null
  }
    const data=props.comments.data;
  
    return (
      <div className='comWrapper'>
        <h2>2条评论</h2>
        <div className='content'>
        {data.map(() => (
            <div className="commentItem">
            <header>
              <img src='http://pic.58pic.com/58pic/11/29/81/73A58PICZc9.jpg'></img>
            <span>zhizi</span>
           </header>
          <p>hhhhahaklgoarjtwo就是A经过LA几个OI酱生日管理理论机构来说就好哦提交iherghg;agljjgoqjoehgrth;ajgerojtjh</p>
         </div>
        ))}
        </div>
        <div className='input'>
           <input placeholder='写下你的评论...'></input>
           <button>发布</button>
        </div>
      </div>
  
    );
  }

