import React, { Component } from 'react';
import { Input,Button ,Select ,Divider} from 'antd';
// import PropTypes from 'prop-types';
import api from '../../api/index'
import './login.scss'

const Option = Select.Option;
class Login extends Component {
  constructor(props){
    super(props);
    this.state={
      a:'sign',
      phoneVal:'',
      passVal:''
    }
  }
   handleSubmit=()=>{
    // console.log(this.state.phoneVal)
    // console.log(this.state.passVal)
    api.postUser({
      phoneVal:this.state.phoneVal,
      passVal:this.state.passVal
    }).then((( {data} )=>{
      //  localStorage.setItem('userName', data.userName);
      this.props.history.push('/')
    }))
  }
  render() {
    
    
    return (
      <div className="login">
         <div className='card'>
            <div className='header'>
              <div>知乎</div>
              <div>{this.state.a=='sign'?'注册':'登录'}知乎，发现更多可信赖的解答</div>
            </div>
            <div className='table'>
             <div className='inputItem'>
              {this.state.a=="sign"?
               <div>
                <Select defaultValue="jack" className='select'>
               <Option value="jack">中国+86</Option>
               <Option value="lucy">香港+856</Option>
               <Option value="disabled">美国+1</Option>
               <Option value="Yiminghe">日本+81</Option>
            </Select>
                <Divider type="vertical" />
             </div>:
             <div></div>
            }
        
       
            <Input size="large" 
                   placeholder={this.state.a=='sign'?'手机号':'手机号或邮箱'} 
                   value={this.state.phoneVal}
                   onChange={(e)=>{this.setState({phoneVal:e.target.value})}} />

            </div>
            <div className='inputItem'>
            <Input size="large" 
                   placeholder={this.state.a=='sign'?'请输入密码':'密码'} 
                   value={this.state.passVal}
                   onChange={(e)=>{this.setState({passVal:e.target.value})}}/>
            </div>
            <Button type="primary" onClick={this.handleSubmit}>{this.state.a=='sign'?'注册':'登录'}</Button>
            </div>
            <div className='text'>
            {this.state.a=='sign'?'注册即代表同意《知乎协议》《隐私保护指引》':'二维码登录'}
            </div>
            <div className='footer'>
            {this.state.a=='sign'?  
            <div>
              <span>已有帐号？</span>
              <span onClick={()=>{this.setState({a:'login'})}}>登录</span>
           </div>:
              <div>
              <span>没有帐号？</span>
              <span onClick={()=>{this.setState({a:'sign'})}}>注册</span>
            </div>
             }
           
            </div>
          </div>
      </div>
    );
  }
}

export default Login;