import React, { Component } from "react";
import { Icon } from "antd";
import CommentItem from '../commentitem/commentItem'
import TopicContent from '../TopicContent/TopicContent'
import api from '../../api/index';
import "./cardItem.scss";

class cardItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      status: "empty",
      comments:[],
      showComments:false,
      flag:true,
      showContent:false,
    };
  }
  handleComments=() => {
  if(this.state.flag){
    this.setState({ flag:false})
    api.getComments().then(res => {
      this.setState({ comments:res.data })
  }).then(()=>{
    this.setState({showComments:!this.state.showComments})
  });
  }else{
    this.setState({showComments:!this.state.showComments})
  }
  
 console.log(this.state.showComments)
}
  handleMore=() =>{
   this.setState({showContent:!this.state.showContent})
}
  render() {
    const dataItem = this.props.data;
    // console.log(dataItem);
    return (
      <div>
        <div className="cardItem">
          <header>{dataItem.highlight.title}</header>
          <div className="content" >
          {/* {this.state.showContent?style={}:style={}} */}
          <div style={!this.state.showContent?{display:'block'}:{display:'none'}}>
            {dataItem.object.author.name}：{dataItem.highlight.description}
           <span className="ContentItem-more" onClick={this.handleMore}>
            &nbsp;阅读全文 &nbsp;
            <Icon type="down" />
          </span>
          </div>
          <TopicContent 
           is={this.state.showContent} 
           content={dataItem.object.content}
           changeContent={this.handleMore}
           ></TopicContent>
        </div>
          <div className="other">
          <span>
            <button
              className={this.state.status === "like" ? "like" : ""}
                onClick={() => {
                if (this.state.status === "like") {
                  this.setState({ status: "empty" });
                } else {
                  this.setState({ status: "like" });
                }
              }}
            >
              <Icon type="like" />
            </button>
            <button
              className={this.state.status === "dislike" ? "dislike" : ""}
              onClick={() => {
                if (this.state.status === "dislike") {
                  this.setState({ status: "empty" });
                } else {
                  this.setState({ status: "dislike" });
                }
              }}
            >
              <Icon type="dislike" />
            </button>
          </span>

          <span onClick={this.handleComments}>
            <Icon type="message" /> 
            {!this.state.showComments?'77条评论':'收起评论'}
          </span>
        </div>
      </div>
       <CommentItem is={this.state.showComments} comments={this.state.comments}></CommentItem>

      </div>
     
    );
  }
}

export default cardItem;
